document.addEventListener("DOMContentLoaded", function () {
  try {
    darkMode();
    headerFijo();
    formulario();
    Admin();
    menuHamburguesa();
  } catch (error) {
    console.log("");
  }
});

function formulario() {
  const abrirPopup = document.getElementsByClassName("btn-abrir-popup");
  const cerrarPopup = document.querySelector(".btn-cerrar-popup");
  const overlay = document.querySelector(".overlay-popup");
  const body = document.querySelector("body");
  const rbemail = document.querySelector(".contactar-email");
  const rbetelefono = document.querySelector(".contactar-telefono");
  const preferencia = document.querySelector(".fecha-hora");
  // const checkbox = document.querySelector(".terminos-condiciones");
  const formacontacto = document.querySelector(".forma-contacto");
  // const btnsubmit = document.querySelector(".btn-azul2"); //boton-gris

  for (i = 0; i < abrirPopup.length; i++) {
    abrirPopup[i].addEventListener("click", function () {
      body.classList.add("fijar-body");
      overlay.classList.add("active");
      if (rbetelefono.checked) {
        preferencia.classList.add("visible");
      } else {
        preferencia.classList.remove("visible");
      }
      formacontacto.classList.remove("no-visible");
    });
  }

  //Cuando se presiona, se cierra
  cerrarPopup.addEventListener("click", function () {
    preferencia.classList.remove("visible");
    overlay.classList.remove("active");
    body.classList.remove("fijar-body");
    formacontacto.classList.add("no-visible");
  });

  //Cuando se da click fuera de la imagen, cerrarla
  overlay.addEventListener("click", function (e) {
    if (e.target == this) {
      //Hace que el click solo funcione para el padre, si se agrega !== hace lo contrario
      preferencia.classList.remove("visible");
      overlay.classList.remove("active");
      body.classList.remove("fijar-body");
      formacontacto.classList.add("no-visible");
    }
  });

  rbetelefono.addEventListener("click", function () {
    preferencia.classList.add("visible");
  });

  rbemail.addEventListener("click", function () {
    preferencia.classList.remove("visible");
  });
}

function headerFijo() {
  const header = document.querySelector(".header");
  const navegacion = document.querySelector(".navegacion");
  window.addEventListener("scroll", function () {
    navegacion.classList.toggle("sticky", window.scrollY > 0);
    header.classList.toggle("sticky", window.scrollY > 0);
  });
}

function darkMode() {
  const preferencia = window.matchMedia("(prefers-color-scheme: dark)");
  const btnDarkMode = document.querySelector(".dark-mode-boton");
  const menu = document.querySelector(".menu");
  const close = document.querySelector(".close");

  //Preferencia darkmode automatico
  if (preferencia.matches) {
    document.body.classList.add("dark-mode");
    btnDarkMode.src = "../build/img/sun.svg";
  } else {
    document.body.classList.remove("dark-mode");
    btnDarkMode.src = "../build/img/dark-mode.svg";
  }

  preferencia.addEventListener("change", function () {
    if (preferencia.matches) {
      document.body.classList.add("dark-mode");
      btnDarkMode.src = "../build/img/sun.svg";
    } else {
      document.body.classList.remove("dark-mode");
      btnDarkMode.src = "../build/img/dark-mode.svg";
    }
  });

  //Preferencia dark mode con click al boton
  btnDarkMode.addEventListener("click", function () {
    document.body.classList.toggle("dark-mode");
    //Para que se guarde el modo oscuro y no se elimine al recargar la página
    if (document.body.classList.contains("dark-mode")) {
      localStorage.setItem("dark-mode", true); // localstorage.setItem(Key,value)
      btnDarkMode.src = "../build/img/sun.svg";
      menu.classList.remove("active");
      close.classList.remove("active");
    } else {
      localStorage.setItem("dark-mode", false);
      btnDarkMode.src = "../build/img/dark-mode.svg";
      menu.classList.remove("active");
      close.classList.remove("active");
    }
  });

  if (localStorage.getItem("dark-mode") === "true") {
    document.body.classList.add("dark-mode");
    btnDarkMode.src = "../build/img/sun.svg";
  } else {
    document.body.classList.remove("dark-mode");
    btnDarkMode.src = "../build/img/dark-mode.svg";
  }
}

function menuHamburguesa() {
  const menu_iconos = document.querySelector(".menu-iconos");
  const item_navegacion = document.querySelectorAll(".lista a");
  const lista = document.querySelector(".lista");
  // const filter = document.querySelector(".filter");
  const menu = document.querySelector(".menu");
  const close = document.querySelector(".close");

  try {
    menu_iconos.addEventListener("click", function () {
      menu_iconos.classList.toggle("active");
      lista.classList.toggle("active");
      // filter.classList.toggle("active");
    });

    item_navegacion.forEach((item) => {
      item.addEventListener("click", function () {
        menu_iconos.classList.remove("active");
        lista.classList.remove("active");
        // filter.classList.remove("active");
      });
    });

    menu.addEventListener("click", function () {
      close.classList.add("active");
      menu.classList.add("active");
    });

    close.addEventListener("click", function () {
      menu.classList.remove("active");
      close.classList.remove("active");
    });
  } catch (error) {}
}
